/*
When we open up a table of categories,
we will want to know how many matches there are in the search data...
*/

import settings from "../../../config.json";
import { rawData } from "../../../rawData";

const { dataSettings } = settings;
const { uid } = dataSettings;

const getCountsMatchingBoth = (
  rawData,
  categoricalColumn,
  filteredIdsBasedOnSearchBox,
  filteredIdsBasedOnPrecisionSearch,
  filteredIdsBasedOnCheckboxes,
  advancedSearchOn,
  categoriesAndCounts
) => {
  let mappedData;

  if (advancedSearchOn) {
    const filteredData = rawData
      .filter((d) => filteredIdsBasedOnPrecisionSearch.includes(d[uid]))
      .filter((d) => filteredIdsBasedOnCheckboxes.includes(d[uid]));
    mappedData = filteredData.map((d) => d[categoricalColumn]);
  } else {
    const filteredData = rawData
      .filter((d) => filteredIdsBasedOnSearchBox.includes(d[uid]))
      .filter((d) => filteredIdsBasedOnCheckboxes.includes(d[uid]));
    mappedData = filteredData.map((d) => d[categoricalColumn]);
  }

  const results = {};
  for (const categoryAndCount of categoriesAndCounts) {
    const { category } = categoryAndCount;
    results[category] = 0;
  }

  for (const category of mappedData) {
    results[category] += 1;
  }

  return results;
};

export { getCountsMatchingBoth };
