import React, { Fragment } from "react";

import Accordion from "@mui/material/Accordion";
import AccordionSummary from "@mui/material/AccordionSummary";
import AccordionDetails from "@mui/material/AccordionDetails";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import Typography from "@mui/material/Typography";

import FilterTableModal from "./FilterTableModal";

function FilterTableModals({ categoricalColumns, dataSettings }) {
  const renderFilters = () => {
    return categoricalColumns.map((exampleCategoricalColumn) => {
      const { categoricalColumn, categoriesAndCounts } =
        exampleCategoricalColumn;
      const categoricalColumnDisplay =
        dataSettings["display"][categoricalColumn];

      return (
        <FilterTableModal
          categoricalColumn={categoricalColumn}
          categoricalColumnDisplay={categoricalColumnDisplay}
          categoriesAndCounts={categoriesAndCounts}
          key={categoricalColumn}
        ></FilterTableModal>
      );
    });
  };
  return (
    <Accordion>
      <AccordionSummary
        expandIcon={<ExpandMoreIcon />}
        aria-controls="panel1a-content"
        id="panel1a-header"
      >
        <Typography>Additional Filters</Typography>
      </AccordionSummary>
      <AccordionDetails>{renderFilters()}</AccordionDetails>
    </Accordion>
  );
}

export default FilterTableModals;

