import * as React from "react";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import Typography from "@mui/material/Typography";

import FilterTableModals from "./FilterTableModals";
import InfoOnSelectedCheckboxes from "./InfoOnSelectedCheckboxes";
import FilterTableModalsInCollapsible from "./FilterTableModalsInCollapsible";

export default function BasicCard({ dataSettings, categoricalColumns }) {
  return (
    <Card>
      <CardContent>
        <Typography variant="h5" gutterBottom component="div">
          Filters
        </Typography>

        <InfoOnSelectedCheckboxes></InfoOnSelectedCheckboxes>
        <FilterTableModals
          dataSettings={dataSettings}
          categoricalColumns={categoricalColumns.filter(
            (d) => d["type"] === "primary"
          )}
        ></FilterTableModals>

        <FilterTableModalsInCollapsible
          dataSettings={dataSettings}
          categoricalColumns={categoricalColumns.filter(
            (d) => d["type"] === "secondary"
          )}
        ></FilterTableModalsInCollapsible>
      </CardContent>
    </Card>
  );
}
