import React from "react";
import Checkbox from "@mui/material/Checkbox";

import { useSelector, useDispatch } from "react-redux";

function StagingCheckbox({ uid }) {
  const idsOfItemsInStagingArea = useSelector(
    (state) => state.idsOfItemsInStagingArea
  );
  const checked = idsOfItemsInStagingArea.includes(uid);

  const dispatch = useDispatch();

  const handleClick = () => {
    dispatch({ type: "HANDLE_STAGING_CHECKBOX_CLICK", payload: uid });
  };

  return (
    <Checkbox
      size="small"
      sx={{ width: 10, height: 10 }}
      onClick={handleClick}
      checked={checked}
      inputProps={{ "aria-label": "controlled" }}
    />
  );
}

export default StagingCheckbox;
