import React, { Fragment } from "react";
import Typography from "@mui/material/Typography";

import Grid from "@mui/material/Grid";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";

function LinksToScripts({ baseURLforScripts, table_name }) {
  const baseURL = baseURLforScripts;

  const nameOfRScript = `${table_name}.r`;
  const linkToRScript = `${baseURL}/import-data-r/${nameOfRScript}`;

  const nameOfSasScript = `${table_name}.sas`;
  const linkToSasScript = `${baseURL}/import-data-sas/${nameOfSasScript}`;

  const nameOfStataScript = `${table_name}.do`;
  const linkToStataScript = `${baseURL}/import-data-stata/${nameOfStataScript}`;

  return (
    <Fragment>
      <Typography variant="h6">Code to Load and Label Parent CSV </Typography>
      <Grid container spacing={2}>
        <Grid item xs={4}>
          <Card>
            <CardContent>
              <a href={`${linkToRScript}`} download={`${nameOfRScript}`}>
                R
              </a>
            </CardContent>
          </Card>
        </Grid>
        <Grid item xs={4}>
          <Card>
            <CardContent>
              <a href={`${linkToSasScript}`} download={`${nameOfSasScript}`}>
                SAS
              </a>
            </CardContent>
          </Card>
        </Grid>
        <Grid item xs={4}>
          <Card>
            <CardContent>
              {" "}
              <a
                href={`${linkToStataScript}`}
                download={`${nameOfStataScript}`}
              >
                Stata
              </a>
            </CardContent>
          </Card>
        </Grid>
      </Grid>
    </Fragment>
  );
}

export default LinksToScripts;
