import React, { Fragment } from "react";

import Grid from "@mui/material/Grid";

import Typography from "@mui/material/Typography";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";

function LinkToData({ table_name, baseURLforData }) {
  const nameOfDataFile = `${table_name}.csv`;
  const linkToDataFile = `${baseURLforData}/${nameOfDataFile}`;

  return (
    <Fragment>
      <Typography variant="h6">Download the Parent CSV File </Typography>

      <Typography variant="body">
        Permission to access the BLSA Data Science SharePoint Online site is
        required.
      </Typography>

      <Grid container spacing={2}>
        <Grid item xs={12}>
          <Card>
            <CardContent>
              <a href={`${linkToDataFile}`} download={`${nameOfDataFile}`}>
                {`${table_name}.csv`}
              </a>
            </CardContent>
          </Card>
        </Grid>
      </Grid>
    </Fragment>
  );
}

export default LinkToData;
