/*
When we open up a table of categories,
we will want to know how many matches there are based on selected checkboxes...
*/

import settings from "../../../config.json";

const { dataSettings } = settings;
const { uid } = dataSettings;

const getCountsMatchingCheckboxFilters = (
  rawData,
  categoricalColumn,
  filteredIdsBasedOnCheckboxes,
  categoriesAndCounts
) => {
  const filteredData = rawData.filter((d) =>
    filteredIdsBasedOnCheckboxes.includes(d[uid])
  );

  const mappedData = filteredData.map((d) => d[categoricalColumn]);

  const results = {};
  for (const categoryAndCount of categoriesAndCounts) {
    const { category } = categoryAndCount;
    results[category] = 0;
  }

  for (const category of mappedData) {
    results[category] += 1;
  }

  return results;
};

export { getCountsMatchingCheckboxFilters };
