import React from "react";

import FormGroup from "@mui/material/FormGroup";
import FormControlLabel from "@mui/material/FormControlLabel";
import Switch from "@mui/material/Switch";
import Tooltip from "@mui/material/Tooltip";

import { useSelector } from "react-redux";

function UnendorsedToggle({
  includeUnsurfacedVariables,
  handleUnendorsedChange,
}) {
  const showIncludeEndorsedVariablesToggle = useSelector(
    (state) => state.showIncludeEndorsedVariablesToggle
  );

  if (showIncludeEndorsedVariablesToggle) {
    return (
      <Tooltip title="The URL you provided determines which variables are endorsed and which are not">
        <FormGroup>
          <FormControlLabel
            control={
              <Switch
                checked={includeUnsurfacedVariables}
                onChange={handleUnendorsedChange}
                size="small"
              />
            }
            label="Include Unendorsed Variables"
          />
        </FormGroup>
      </Tooltip>
    );
  } else {
    return null;
  }
}

export default UnendorsedToggle;
